.item {
  margin: 1rem 0;
  background-color: #F5EDED;
  padding: 1rem;
  border-radius: 10px;
}

.item h3 {
  margin: 0 0 0.5rem 0;
  font-size: 1.75rem;
}

.item header {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}

.details {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.quantity span {
  font-size: 1.5rem;
  font-weight: bold;
}

.price {
  font-size: 1.5rem;
  font-weight: bold;
}

.itemprice {
  font-weight: normal;
  font-size: 1rem;
  font-style: italic;
}

.actions {
  display: flex;
  justify-content: flex-end;
  margin: 0.5rem 0;
}

.actions button {
  background-color: #144272;
  border: 1px solid #144272;
  margin-left: 0.5rem;
  padding: 0.15rem 1rem;
  color: #F5EDED;
  font-weight: 600;
  transition: 0.2s background ease-in-out;
}

.actions button:hover,
.actions button:active {
  background-color: #F5EDED;
  color: #144272;
}